              text: '一卧室',
              id: '1'
            },
            {
              text: '一室一厅',
              id: '2'
            },
            {
              text: '一室一厅+书房',
              id: '3'
            },
            {
              text: '两室一厅',
              id: '4'
            },
            {
              text: '两室一厅+书房',
              id: '5'
            },
            {
