            }
          ]
        },
        '狮城租房': {

        }
      }
      return data[this.category]
    }
  },
  methods: {
    filterClick (type, id) {
      const data = {
        '地区': 'region',
        '价格': 'price',
        '面积': 'area',
        '户型': 'house'
      }
      type = data[type]
      let query = this.$route.query
      query[type] = query[type] ? query[type].split(',') : []
      query[type].push(id)
      query[type] = query[type].toString()
      const path = this.$route.path
      this.$router.push({
        path: `${path}?${query}`
      })
    }
  }
}
</script>
